import React, { Component } from "react";
import ReactApexChart from "react-apexcharts";
import { Card, Row, Col } from "react-bootstrap";
import { connect } from 'react-redux';
import NoCardData from "../../CommonComponents/NotFound/NoCardData";
import withRouter from "../../../../routes/WithRouter";
import { checkPermission } from "../../../../../actions/collaboratorActions";

class ContentChart extends Component {
    constructor(props) {
        super(props);

        this.state = {
            series: [1, 1],
            options: {
                dataLabels: { enabled: false },
                legend: { show: false },
                colors: ['#4e69ed', '#00e396'],
                chart: { type: 'donut' },
                plotOptions: {
                    pie: {
                        donut: {
                            stroke: 0,
                            legend: { show: false },
                            labels: {
                                show: true,
                                total: { label: 'Loading...', showAlways: true, show: true },
                                value: { show: false }
                            }
                        }
                    }
                },
                chart: {
                    animations: {
                        enabled: true,
                        easing: 'easeinout',
                        speed: 800,
                        animateGradually: { enabled: true, delay: 2000 },
                        dynamicAnimation: { enabled: true, speed: 350 }
                    }
                },
                tooltip: { enabled: false },
                labels: ['UGC', 'Branded']
            },
            brand: 0,
            ugcContent: 0,
            loader: true
        }
    };

    componentDidMount() {
        const { ugc } = this.props
        if (ugc) this.onLoadBrandUGC(ugc[0].data);
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.ugc != this.props.ugc)
            this.onLoadBrandUGC(nextProps.ugc[0].data)
    }

    onLoadBrandUGC = (data) => {
        this.setState(prevState => ({
            ugcContent: data[0],
            brand: data[1],
            series: data,
            options: {
                ...prevState.options,
                plotOptions: {
                    ...prevState.options.plotOptions,
                    pie: {
                        ...prevState.options.plotOptions.pie,
                        donut: {
                            ...prevState.options.plotOptions.pie.donut,
                            labels: {
                                show: true,
                                total: { label: 'UGC vs Branded', showAlways: true, show: true },
                                value: { show: true }
                            }
                        }
                    }
                },
                tooltip: { enabled: true }
            },
            loader: data[0] == 0 && data[1] == 0 ? false : true
        }))
    }
    render() {
        const { options, series, brand, ugcContent, loader, wallId } = this.state
        return <Card className="card-stretch gutter-b">
                    <div className="f-between p-3 py-2 py-sm-3 p-sm-5 border-bottom">
                        <span className="h5 mb-0">UGC V/S Branded content</span>
                    </div>
                    <Card.Body>
                        <div style={{minHeight:300}}>
                            {loader ? <Row className="justify-content-center">
                                    <Col lg={9} xl={7}>
                                        <ReactApexChart options={options} series={series} type="donut" className="assetsType w-100 m-auto" style={{maxWidth:350}}/>
                                    </Col>
                                    <Col lg={12} xl={5} className="f-center justify-content-xl-start">
                                        <div className="px-3 w-100" style={{maxWidth:200}}>
                                            <div className="f-between py-2">
                                                <p className="fs-9 fw-medium d-flex align-items-center mb-0"><span className="h-16px w-16px rounded-1 me-2" style={{backgroundColor:'#4e69ed'}}></span>UGC posts</p>
                                                <span className="fw-bolder fs-8">{ugcContent}</span>
                                            </div>
                                            <div className="f-between py-2">
                                                <p className="fs-9 fw-medium d-flex align-items-center mb-0"><span className="h-16px w-16px rounded-1 me-2" style={{backgroundColor:'#00e396'}}></span>Branded content</p>
                                                <span className="fw-bolder fs-8">{brand}</span>
                                            </div>
                                        </div>
                                    </Col>
                                </Row> : 
                                <NoCardData title="Need More UGC" desc="UGC performs 56% better than branded content on-site. Leverage Snapup to collect reviews & ratings." methodTitle="Add content" method={()=> checkPermission('networkManager', 'manageFeed', true) ? this.props.navigate(`/content/addfeed/${localStorage.getItem('wallId')}`) : {}} />
                            }
                        </div>
                    </Card.Body>
                </Card>
    }
}

const mapStateToProps = state => {
    return {
        ugc: state.dashboardData.ugc
    }
}

export default connect(mapStateToProps)(withRouter(ContentChart))