import React, { Component } from 'react';
import { Button, Row, Col } from 'react-bootstrap';

class NoCardData extends Component {

       render() {
              const { title, desc, method, methodTitle, subTitle, subMethod } = this.props
              return <div className={`f-center py-4 h-100 w-100 flex-column`} style={{minHeight:200}}>
                            <div className="d-flex flex-column align-items-center w-100">
                                   <Row  className="mb-2 text-center justify-content-center gx-0 w-100">
                                          <Col xxl={8} xl={10}>
                                          <h4 className={`text-gray-dark fw-semibold text-center`}>{title}</h4>
                                          {desc ? <p>{desc}</p> : null}
                                          </Col>
                                   </Row>
                            </div>
                            {method ?
                            <div className="d-flex">
                                   <Button variant='primary' className="m-2 btn-min-w" onClick={()=>method()}>
                                          {methodTitle}
                                   </Button>
                                   {subTitle ? <Button variant='secondary' className="m-2 btn-min-w" onClick={()=>subMethod()}>
                                          {subTitle}
                                   </Button> : null}
                            </div> 
                            : null}
                     </div>
       }
}

export default NoCardData;